h1.Heading {
  font-size: 90px;
  color: white;

  span {
    color: rgba(231, 210, 27, 1);
  }

  @media screen and (max-width: 900px) {
    font-size: 70px;
  }

  @media screen and (max-width: 600px) {
    font-size: 60px;
  }
}

h2.Heading {
  color: white;
  font-size: 25px;
  background: rgba(96, 104, 117, 0.5);
  padding: 5px 0px;

  @media screen and (max-width: 900px) {
    font-size: 20px;
  }
}

.Heading {
  word-spacing: 4px;
  letter-spacing: 1px;
  margin-bottom: 15px;
}
